import React, { useContext, useEffect, useRef } from "react";
import "./Services.css";
import { themeContext } from "../../Context";
import Desfer from './desfer-john-enriquez.pdf';
import lottie from 'lottie-web';

const Services = () => {

    //lottie animation
    const container = useRef(null)

    useEffect(() => {
      lottie.loadAnimation({
        container: container.current,
        renderer: 'svg',
        loop: true,
        autoplay: true,
        animationData: require('../../img/65677-photo-editing-animation.json')
      })
    }, [])
  // context
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  return (
    <div className="services" id="services">
      {/* left side */}
      <div className="awesome-0">
        {/* dark mode */}
        <span style={{ color: darkMode ? "white" : "" }}>My Awesome</span>
        <span>services</span>
        <spane>
        I am savvy in using the tools and software that’s 
        <br />
        being utilized by online world. I can create websites 
        <br />
        with amazing designs, functional links, responsive pages, 
        <br />
        great animations and even add security.
        </spane>
        <a href={Desfer} download>
          <button className="button s-button">Download CV</button>
        </a>
        <div className="blur s-blur1" style={{ background: "#ABF1FF94" }}></div>
      </div>
    <div className="image-box">
      <div className="container" ref={container}></div>
    </div>
    </div>
  );
};

export default Services;
